.navbar-menu a {
  letter-spacing: 0.7px;
}

.navbar-menu a::before {
  content: '';
  display: block;
  position: absolute;
  bottom: 3px;
  left: 0;
  height: 3px;
  width: 100%;
  background-color: #f7d549;
  transform-origin: right top;
  transform: scale(0, 1);
  transition: transform 0.15s ease-out;
}

.navbar-menu a:hover::before {
  transform-origin: left top;
  transform: scale(1, 1);
}

.navbar-menu a:hover,
a:active {
  font-weight: bold;
}

footer {
  background-color: #1A1919;
  color: #fff;
  font-size: 13px;
}

footer #heart-icon {
  color: #e4405f;
}

#back-to-top {
  position: fixed;
  bottom: 20px;
  right: 23px;
  font-size: 22px;
  padding: 2px 13px;
  border-radius: 0;
  display: none;
  color: whitesmoke;
  background-color: #454343;
  border-width: 2px;
  border: 2px solid #1A1919;
  transition: background-color 0.3s, border 0.3s, color 0.3s;
}

#back-to-top:hover,
#back-to-top:active {
  color: whitesmoke;
  background-color: #1A1919;
}

#special-hover p:after {
  content: " and smol helper Emily Dang & noodle";
  opacity: 0;
  transition: 0.4s ease;
}

#special-hover:hover p:after {
  opacity: 1;
  font-size: 10px;
}

@keyframes spinAround {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(359deg);
  }
}

